import { Injectable } from '@angular/core';
import { NgxSpinnerService } from 'ngx-spinner';
import { Subscription } from 'rxjs';
import { ISpinnerService } from './spinner.interface';

const PRIMARY_SPINNER = 'primary';

const DismissingDelayPeroid = 300;
const DefaultShowingDelayPeroid = 500;

/* Note that on purpose we do not turn this one into a singular service. 
 * Therefore, we are able to create many such services for each component */

@Injectable()
export class SpinnerServiceImpl implements ISpinnerService {

    private _showingTimer: any;
    private _showingDelay: number;
    private _dismissingTimer: any;

    private _spinnerState: boolean;
    private _referenceCounter = 0;

    private _subr: Subscription;

    constructor(private readonly _underlyingSpinner: NgxSpinnerService) {
        this._showingTimer = 0;
        this._showingDelay = DefaultShowingDelayPeroid;
        this._dismissingTimer = 0;

        this._spinnerState = false;
    }

    // Note that we do not need to stop it, as this is a service starting in the beginning.
    public startToListenSpinner(name: string = PRIMARY_SPINNER) {
        // Set up the listener
        this._subr = this._underlyingSpinner.getSpinner(name).subscribe(x => {
            this._spinnerState = x.show;
        });
    }

    public stopListener(name: string = PRIMARY_SPINNER) {
        this._subr && this._subr.unsubscribe();
    }

    public setDelay(seconds: number) {
        this._showingDelay = seconds * 1000;
    }

    // Override
    public show(title: string = 'Loading ...', name = PRIMARY_SPINNER) {

        this._referenceCounter++;

        // If there is one already, use it.
        if (this._spinnerState) {

            // However, we need to cancel the dismiss timer.
            // It is safe, because we expect that "hide" is to be called
            // sometime later from this moment on.
            if (this._dismissingTimer) {

                clearTimeout(this._dismissingTimer);
                this._dismissingTimer = 0;
            }

            return;
        }

        // If we have already scheduled to dismiss the spinner,
        // we just need to clear the scheduler.
        // Please refer to the above for the reason.
        if (this._dismissingTimer) {

            clearTimeout(this._dismissingTimer);
            this._dismissingTimer = 0;
        }

        // If we have already scheduled to show the spinner, we just
        // use this schedule. 
        if (this._showingTimer) {

            return;
        }

        // Otherwise, schdule to show the spinner.
        this._showingTimer = setTimeout(() => {

            if (this._showingTimer) {
                // Clean up the timer
                this._showingTimer = 0;
                this._underlyingSpinner.show(name);
            }

        }, this._showingDelay);

    }

    public hide(name = PRIMARY_SPINNER) {

        this._referenceCounter--;

        if (this._referenceCounter > 0) {

            return;
        }

        // If the spinner has not been scheduled.
        if (this._showingTimer) {

            clearTimeout(this._showingTimer);
            this._showingTimer = 0;

            // Done
            return;
        }

        // If have scheduled to dismiss the spinner,
        // we better we schedule again.
        if (this._dismissingTimer) {

            clearTimeout(this._dismissingTimer);
            this._dismissingTimer = setTimeout(() => {

                if (this._dismissingTimer) {
                    // Clean up the timer
                    this._dismissingTimer = 0;
                    // Dismiss the spinner 
                    this._underlyingSpinner.hide(name);
                }
            }, DismissingDelayPeroid);

            return;
        }

        // Schedule to dismiss the spinner

        if (this._spinnerState) {

            this._dismissingTimer = setTimeout(() => {

                if (this._dismissingTimer) {

                    this._dismissingTimer = 0;
                    // Dismiss the spinner 
                    this._underlyingSpinner.hide(name);
                }
            }, DismissingDelayPeroid);
        }
    }
}

